.app{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding:40px 24px;
}


.app__searchForm{
  margin-top: 18px;
}

.app__input{
  padding:6px 16px; 
  min-width: 200px;
  max-width: 100vw;
  font-size:15px; 
}

.app__submit{
  padding:6px 16px;
  font-size:15px;
  margin-left:6px;
  background-color:#02aff0;
  border-radius: 4px;
  border:2px solid #02aff0;
  color:white;
}
.app__submit:hover{
  padding:6px 16px;
  font-size:15px;
  margin-left:6px;
  background-color:white;
  border-radius: 4px;
  border:2px solid #02aff0;
  color:#02aff0;
  cursor:pointer;
}
.app__recipes{
  margin-top:24px;
  display: grid;
  grid-template-columns: 230px 230px;
  align-items: baseline;
}

.app__healthLabels{
  padding:7px 8px;
  margin-left: 6px;
  border-radius: 4px;
}